export function normalizeReturnRender(h, content) {
  if (content === null || content === undefined) {
    return ''
  }

  //  
  if (content.constructor.name === 'VNode') {
    if (content.tag === 'template') {
      // , span
      content.tag = 'div'
    }
    return content
  }

  // , 
  return <span>{content}</span>
}
