const root = {
  state: {
    currentRoute: null, // 
    menuCollapse: false, // 
    screen: {} // 
  },
  mutations: {
    currentRouteUpdate(state, payload) {
      state.currentRoute = payload
    },
    menuCollapseUpdate(state, payload) {
      state.menuCollapse = payload
    },
    screenUpdate(state, payload) {
      state.screen = payload
    }
  }
}

export default root
